import React, { Component } from 'react';
import Navigation from '../navigation';
import Explorer from '../explorer';

class Home extends Component {
  render() {
    return (
      <div className='h-100 no-scroll'>
        <Navigation />
        <Explorer />
      </div>
    )
  }
}

export default Home;



